<template>
  <div class="container text-center">
    <div class="row mt-sm-5">
      <div class="text-center fw-bold fs-4 mb-3 mt-2">
        {{ conferenceDescription }}
      </div>
      <div class="col-12 offset-md-4 col-md-4 bg-white login-frm p-4 shadow">
        <!-- Tabs Titles -->

        <!-- Icon -->
        <div class="fadeIn first">
          <img class="mb-2" style="width: 100px" src="../assets/logo.svg" />
          <h6 class="mb-2 fs-3 fw-bold text-center">
            {{ $route.meta.title }}
          </h6>
        </div>

        <!-- Login Form -->
        <div
          v-if="error"
          class="bg-danger fw-bold mb-4 px-4 py-2 rounded text-white"
        >
          {{ error }}
        </div>
        <div>
          <div v-if="showPhone">
            <div class="mb-1 text-start">
              <label for="login" class="form-label">
                جهت ورود یا عضویت تلفن همراه خود را وارد کنید</label
              >
              <div class="input-group">
                <span class="input-group-append">
                  <span
                    v-if="mobileIsValid"
                    class="btn rounded-pill border-0 mt-2 text-secondary"
                  >
                    <i class="fas fa-check text-success"></i>
                  </span>
                  <span
                    v-else
                    class="btn rounded-pill border-0 mt-2 text-secondary"
                  >
                    <i class="fas fa-exclamation-triangle"></i>
                  </span>
                </span>
                <input
                  type="text"
                  class="
                    form-control form-control-lg
                    rounded-pill
                    text-center text-primary
                    mr-1
                    pr-0
                  "
                  id="login"
                  v-model="userPhone"
                  placeholder="0912*******"
                  v-on:keyup="perToEn"
                  dir="ltr"
                />
              </div>
            </div>

            <div class="mb-3 mt-3 text-start">
              <a
                id="registerLoginBtn"
                type="button"
                class="btn btn-theme w-100 fw-bold rounded-pill"
                @click="registerLogin"
              >
                ادامه
                <span v-if="OTPSpin">
                  <i class="fas fa-spinner fa-spin"></i>
                </span>
              </a>
            </div>
          </div>

          <div v-if="showLastname">
            <div class="mb-3 text-start">
              <label for="lastName" class="form-label"
                >نام و نام خانوادگی</label
              >

              <div class="input-group" lang="en">
                <input
                  type="text"
                  id="lastName"
                  class="form-control form-control-lg text-center rounded-pill"
                  v-model="lastName"
                  @keyup.enter="showCodeBox"
                />
              </div>
            </div>

            <div class="mb-3 mt-3 text-start">
              <a
                role="button"
                class="btn btn-theme w-100 fw-bold rounded-pill"
                @click="showCodeBox"
              >
                ادامه
              </a>
            </div>
          </div>

          <div v-if="showCode">
            <div class="mb-3 text-center">
              <label for="otp" class="form-label"
                >کد پیامک شده را وارد فرمایید</label
              >

              <input
                id="partitioned"
                maxlength="4"
                type="text"
                class="d-block fw-bold fs-5"
                v-model="otp"
                dir="ltr"
                @keyup="OTPKeyUp"
                :disabled="OTPSpin"
              />
              <div class="mt-4">
                <button
                  class="btn btn-warning rounded-pill"
                  :disabled="btnResendDisabled"
                  style="min-width: 150px"
                  @click="registerLogin"
                >
                  {{ btnResendText }}
                </button>
              </div>
            </div>

            <div class="mb-3 mt-3 text-center">
              <span v-if="OTPSpin"
                ><i class="fas fa-spinner fa-spin fs-4"></i>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ArefApi from "../libs/api.js";
import utils from "../libs/utils";
export default {
  name: "register",
  created() {
    this.utils = new utils();
    this.arefApi = new ArefApi(this);
  },
  data() {
    return {
      userPhone: this.$route.query.phone ? this.$route.query.phone : "",
      referrerFromUrl: this.$route.query.referrer ? true : false,
      referrer: this.$route.query.referrer,
      error: null,
      lastName: null,
      otp: "",
      mobileIsValid: true,
      OTPSpin: false,
      otpType: null,

      showPhone: true,
      showCode: false,
      showLastname: false,

      pathAfterLogin: this.$route.params.pathAfterLogin
        ? this.$route.params.pathAfterLogin
        : null,
      btnResendText: "ارسال مجدد کد",
      btnResendDisabled: false,
      productDetailVideosId: this.$route.params.product_detail_videos_id,
      conferenceDescription: null,
      conferenceStartMode: null,
      conferenceName: null,
      productId: null,
    };
  },
  methods: {
    registerLogin(event) {
      this.error = null;
      if (!this.mobileIsValid) {
        this.error = "موبایل را صحیح وارد نمایید.";
        return;
      }
      event.target.disbaled = true;
      this.OTPSpin = true;
      const mobiledata = {
        email: this.userPhone,
        product_detail_videos_id: this.productDetailVideosId,
      };
      this.arefApi
        .registerLogin(mobiledata)
        .then((resp) => {
          this.otpType = resp.data.type;
          this.showPhone = false;
          if (this.otpType === "login") {
            this.showCode = true;

            this.btnResendText = 60;
            this.btnResendDisabled = true;
            this.startResendTimer();
            setTimeout(() => {
              document.querySelector("#partitioned").focus();
            }, 500);
          } else if (this.otpType === "register") {
            this.showLastname = true;
          }
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          event.target.disbaled = false;
          this.OTPSpin = false;
        });
    },
    showCodeBox() {
      this.error = null;
      if (!this.lastName || this.lastName.length < 3) {
        this.error = "نام و نام خانوادگی را صحیح وارد کنید";
        return;
      }
      this.showLastname = false;
      this.showCode = true;
      this.btnResendText = 60;
      this.btnResendDisabled = true;
      this.startResendTimer();
      setTimeout(() => {
        document.querySelector("#partitioned").focus();
      }, 500);
    },
    checkCode() {
      this.error = null;
      if (!this.otp || this.otp.length !== 4) {
        this.error = "کد را صحیح وارد کنید";
        return;
      }

      this.OTPSpin = true;
      const mobiledata = {
        email: this.userPhone,
        otp: Number(this.otp),
        last_name: this.lastName,
      };
      const fn = this.otpType === "login" ? "loginWithOtp" : "registerWithOtp";
      this.arefApi[fn](mobiledata)
        .then((response) => {
          this.utils.afterSuccessLogin(response);
          const conferenceData = {
            product_detail_videos_id: this.productDetailVideosId,
            users_id: response.data.user_id,
            referrer: document.referrer || "--",
            already_registerd: this.otpType === "login" ? "yes" : "no",
          };
          this.arefApi.addUserToConference(conferenceData);
          switch (response.data.group.type) {
            case "admin":
              //routeName = "AdminHome";
              break;

            default:
              this.arefApi
                .getCart()
                .then((resp) => {
                  this.$store.commit("set", [
                    "cartCount",
                    this.utils.getCartCount(resp.data),
                  ]);
                  this.$store.commit("set", ["cartAmount", resp.data.amount]);
                })
                .catch((err) => {
                  this.utils.showErrors(this, err);
                });
              break;
          }

          if (this.conferenceStartMode == "playPage") {
            this.$router.push({
              name: "VideoSessionPlay",
              params: {
                productDetailViedosId: this.productDetailVideosId,
                videoSessionName: this.conferenceName,
              },
            });
          } else {
            this.$router.push({
              name: "VideoSession",
              params: {
                videoSessionId: this.productDetailVideosId,
                productId: this.productId,
              },
            });
          }
        })
        .catch((err) => {
          console.log(err);
          //this.utils.showErrors(this, err);
          this.otp = null;
        })
        .finally(() => {
          this.OTPSpin = false;
        });
    },

    checkIsLogged() {
      if (this.$store.state.token) {
        const conferenceData = {
          product_detail_videos_id: this.productDetailVideosId,
          users_id: this.$store.state.user_id,
          referrer: document.referrer || "-",
          already_registerd: "yes",
        };
        this.arefApi.addUserToConference(conferenceData);
        if (this.conferenceStartMode == "playPage") {
          this.$router.push({
            name: "VideoSessionPlay",
            params: {
              productDetailViedosId: this.productDetailVideosId,
              videoSessionName: this.conferenceName,
            },
          });
        } else {
          this.$router.push({
            name: "VideoSession",
            params: {
              videoSessionId: this.productDetailVideosId,
              productId: this.productId,
            },
          });
        }
      }
    },
    perToEn(e) {
      this.userPhone = this.utils.perToEn(this.userPhone);
      this.mobileIsValid = true;
      if (this.userPhone) {
        this.mobileIsValid = this.utils.validMobile(this.userPhone);
      }
      if (e.keyCode === 13) {
        document.querySelector("#registerLoginBtn").click();
      }
    },
    OTPKeyUp(e) {
      this.otp = this.utils.perToEn(this.otp);
      if (this.otp.length === 4) {
        this.checkCode();
      }
      if (e.keyCode === 13) {
        this.checkCode();
      }
    },
    startResendTimer() {
      setTimeout(() => {
        if (this.btnResendText > 0) {
          this.btnResendText--;
          this.startResendTimer();
        } else {
          this.btnResendText = "ارسال مجدد کد";
          this.btnResendDisabled = false;
        }
      }, 1000);
    },
    getConferenceInfo() {
      return this.arefApi
        .getConferenceInfo(this.productDetailVideosId)
        .then((resp) => {
          this.conferenceDescription = resp.data.free_conference_description;
          this.conferenceStartMode = resp.data.free_conference_start_mode;
          this.conferenceName = resp.data.name || "همایش";
          this.productId = resp.data.products_id;
        });
    },
  },
  async mounted() {
    document.querySelector("#login").focus();
    await this.getConferenceInfo();
    this.checkIsLogged();
  },
};
</script>
<style scoped>
.login-frm {
  min-width: 300px;
}
#partitioned {
  padding-left: 15px;
  letter-spacing: 50px;
  border: 0;
  background-image: linear-gradient(
    to left,
    rgb(49, 47, 47) 70%,
    rgba(255, 255, 255, 0) 0%
  );
  background-position: bottom;
  background-size: 58px 1px;
  background-repeat: repeat-x;
  background-position-x: 35px;
  width: 220px;
  margin: 0 auto;
}
#partitioned:focus {
  outline: none;
}
</style>